.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.search-form {
  justify-content: center;
  margin-bottom: 20px;
  display: flex;
}

input[type="text"] {
  width: 300px;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 10px;
  font-size: 16px;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #2196f3;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
}

.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(4, 1fr);
  display: grid;
}

.photo-card {
  border: 1px solid #ccc;
  border-radius: 4px;
  transition: transform .3s;
  overflow: hidden;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.photo-card:hover {
  transform: scale(1.05);
}

.photo-card img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.info {
  color: #333;
  background-color: #f5f5f5;
  padding: 10px;
}

.info-item {
  margin: 5px 0;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #2196f3;
  border: none;
  border-radius: 4px;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 16px;
  display: none;
}

.notification {
  color: #fff;
  background-color: #2196f3;
  border-radius: 4px;
  padding: 10px 20px;
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
}

